.navigation {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  height: 70px;
  background-color: rgb(223, 223, 223);
  align-items: center;
  .pages {
    .links {
      display: flex;
      align-items: center;
      gap: 30px;
      ul {
        display: flex;
        align-items: center;
        gap: 30px;
        list-style-type: none;
        li {
          list-style-type: none;
        }
      }
      .menu {
        text-align: center;
        justify-content: center;
        flex-direction: column;
        align-items: center;
      }
      .cart-icon {
        margin-top: 10px;

        height: 35px;
      }
    }
  }
}

.home-container {
  .homeprodect {
    background-image: url('https://images2.alphacoders.com/912/thumb-1920-912814.jpg');
    background-position: center;
    background-repeat: no-repeat;
    backface-visibility: cover;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  display: flex;
  // justify-content: space-between;
  align-items: center;
  padding: 40px;
  .home-content {
    padding: 20px;
    width: 50%;
    .bikeDelivery {
      background-color: rgb(224, 224, 58);
      color: black;

      padding: 5px;
      border-radius: 3px;
      font-size: 13px;
      margin: 10px;
      outline: none;
      border: none;
    }
    h2 {
      color: rgb(63, 63, 63);
      font-style: normal;
      margin: 10px;
      font-size: 35px;
      font-family: sans-serif;
      width: 300px;
      margin-bottom: 15px;
      span {
        color: orangered;
      }
    }
    p {
      margin: 10px;
      font-size: 17px;
      gap: 5px;
      font-family: sans-serif;
    }
    .order-btn {
      button {
        margin: 10px;
        outline: none;
        border: none;
        color: black;
        padding: 8px;
        border-radius: 5px;
        background-color: orangered;
      }
    }
  }
  .home-prodect {
    opacity: 0.7;
    .prodect-container {
      text-align: center;
      padding: 15px;
      width: 150px;
      margin: 15px;
      margin-right: 30px;
      background-color: rgb(245, 245, 245);
      border-radius: 10px;
      img {
        height: 50px;
        width: 50px;
        margin-bottom: 5px;
        font-family: sans-serif;
        font-size: bold;
      }
      p {
        margin-bottom: 5px;
        span {
          color: orangered;
        }
      }
    }
  }
}

.freshItems {
  padding: 40px;
  margin: 10px;
  .freshContainer {
    margin-bottom: 40px;
    h2 {
      font-family: sans-serif;
      color: black;
      margin: 10px;
    }
    .underline {
      height: 5px;
      width: 200px;
      margin: 10px;
      background-color: orangered;
      border-radius: 3px;
    }
  }
  .fruitsContainer {
    display: flex;
    width: 100%;
    overflow-x: scroll;
    height: auto;
    .fruits {
      width: 300px;
      height: auto;
      background-color: rgb(236, 236, 236);
      border-radius: 6px;
      padding: 13px;
      margin: 15px;

      .f-img {
        display: flex;
        justify-content: space-between;
        div {
          .img {
            height: 60px;
            width: 60px;
            border-radius: 5px;
            transition: 0.4s;
          }
          h2 {
            color: white;
            font-size: 20px;
            padding: 5px;
            border-radius: 55%;
            background-color: orangered;
          }
        }
      }
      sub {
        h3 {
          color: black;
          font-family: sans-serif;
          margin-top: 10px;
          margin-bottom: 10px;
        }
        p {
          color: rgb(56, 56, 56);
          font-family: sans-serif;
          margin-top: 10px;
          margin-bottom: 10px;
        }
        h5 {
          span {
            color: orangered;
          }
          color: black;
          margin-top: 10px;
          margin-bottom: 10px;
        }
      }
    }
    &:hover {
      .img {
        height: 70px;
        width: 70px;
      }
    }
  }
}

